@import "../../theme.scss";

.thumbnail {
  position: relative;
  border: 2px solid #f3f5f7;
  border-radius: 5px;
  overflow: hidden;
  background-color: #d3d3d3;
  background-size: cover;
  cursor: pointer;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    min-width: 100%;
    min-height: 100%;
    transform: translate(-50%, -50%);
  }

  .GIF-IMG {
    position: absolute;
    right: 5px;
    bottom: 5px;
    font-size: 20px;
    color: #fff;
  }

  .v_icon {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 50px;
    transform: translate(-50%, -50%);
    color: #fff;
  }

  .icon {
    position: absolute;
    left: 8px;
    bottom: 4px;
    font-size: 24px;
    color: #fff;
  }

  &:hover {
    border-color: $bg-color;
  }

  .sign {
    position: absolute;
    top: 5px;
    left: 5px;
    height: 15px;
    color: #000;
    text-shadow: 2px 2px 2px #fff;
    border-radius: 3px;
    line-height: 15px;
    font-size: 12px;
  }

  &:hover .icon-check-s {
    visibility: initial;
  }
}

.selected {
  border-color: $bg-color;

  .icon-check-s {
    visibility: initial;
    border: 0;
  }
}